  }

  .site-nav__item:not(:last-child) {
    margin-right: 30px;
  }

  .site-nav__link {
    letter-spacing: -.05em;
    color: var(--text-color-nav);
    font-family: Golos Text, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;
    text-decoration: none;
    display: block;
  }

  .site-nav__link:after {
    content: "";
